import {getMyCouponDetail} from '@/http/product'
export default{
	data(){
		return {
			id:'',
			coupon:{
			}
		}
	},
	computed: {
	    isToken() {
	        return this.$store.state.login ? this.$store.state.login.token :''
	    }
	},
	watch:{
		isToken(){
			this.init();
		}
	},
	methods:{
		init(){
			this.getMyCouponDetailFun();
		},
		getMyCouponDetailFun(){
			getMyCouponDetail({id:this.id}).then(data=>{
				if(data.data.retCode===0){
					this.coupon = data.data.result
				}else{
					uni.showToast({
						title: data.data.retMsg,
						duration: 2000,
						icon:'none'
					});
				}
			}).catch(e=>{
				uni.showToast({
					title: e.data.retMsg,
					duration: 2000,
					icon:'none'
				});
			})
		}
	},
	onLoad(params){
		if(params.id){
			this.id = params.id;
		}
		this.init()
	},
	onShow(){
		if(this.$root.$mp.query.id){
			this.id=this.$root.$mp.query.id;
		}
		this.init()
	}
}
